.navigation {
  ul {
    display: flex;
    margin-left: 40%;
    width: 1px;
    font-size: 30px;

    a {
      text-decoration: none;
      padding: 10px;

      li {
        color: black;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
          color: aquamarine;
        }
      }
    }

  }
}
