.detail-view {
    width: 620px;
    height: 400px;
    padding: 10px;
    background-color: #df095e;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    bottom: 105px;
    left: 50%;
    transform: translateX(-50%);
}

.sprite-image {
    width: 150px;
    height: 150px;
    margin: 10px auto;
    margin-top: 0px;
    background-color: #585858;
    border: 15px solid #dedede;
    border-radius: 5px;
    color: #585858;
}

.data-wrapper {
    width: 80%;
    height: 120px;
    padding: 10px;
    margin: 0 auto;
    background-color: #585858;
    border-radius: 5px;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: #fff;
}

.data-name {
    margin: 0 0 5px;
}

.data-char {
    font-size: 20px;
}
